import React, { lazy } from 'react'
import ReactDOM from 'react-dom/client'
import { ChakraProvider, extendTheme } from '@chakra-ui/react'
import { Provider as ReduxProvider } from 'react-redux'
import { colors } from './styles'
import store from './state'
import App5 from './App5'

const App = lazy(() => import('./App'))
const App2 = lazy(() => import('./App2'))
const App3 = lazy(() => import('./App3'))
const App4 = lazy(() => import('./App4'))

const breakpoints = {
  sm: '576px',
  md: '852px',
  lg: '968px',
  xl: '1080px',
  '2xl': '1200px',
}

const theme = extendTheme({
  styles: {
    global: {
      'html, body': {
        fontFamily: 'Poppins, sans-serif',
        borderColor: 'red',
        background: 'transparent',
      },
    },
  },
  colors,
  breakpoints,
  components: {
    Progress: {
      baseStyle: {
        filledTrack: {
          bg: 'foreground',
        },
      },
    },
  },
})

const Provider = ({ children }) => {
  return (
    <ReduxProvider store={store}>
      <ChakraProvider theme={theme}>{children}</ChakraProvider>{' '}
    </ReduxProvider>
  )
}

const addStyles = function () {
  const linkNode = document.createElement('link')
  linkNode.rel = 'stylesheet'
  linkNode.href = 'https://cdnjs.cloudflare.com/ajax/libs/Swiper/8.4.6/swiper-bundle.min.css'
  linkNode.type = 'text/css'
  document.head.appendChild(linkNode)
}

window.renderFeatured = function () {
  const ele = document.getElementById('bad-featured')
  const accessKey = ele.dataset.accessKey
  const root = ReactDOM.createRoot(ele)
  root.render(
    <React.StrictMode>
      <Provider>
        <App accessKey={accessKey} />
      </Provider>
    </React.StrictMode>,
  )
}

window.renderStats = function () {
  const ele = document.getElementById('bad-stats')
  const accessKey = ele.dataset.accessKey
  const root = ReactDOM.createRoot(ele)
  root.render(
    <React.StrictMode>
      <Provider>
        <App2 accessKey={accessKey} />
      </Provider>
    </React.StrictMode>,
  )
}

window.renderSubscribe = function () {
  const ele = document.getElementById('bad-subscribe')
  const accessKey = ele.dataset.accessKey
  if (!accessKey) return null
  const root = ReactDOM.createRoot(ele)
  root.render(
    <React.StrictMode>
      <Provider>
        <App5 accessKey={accessKey} />
      </Provider>
    </React.StrictMode>,
  )
}

window.renderMenu = function () {
  const ele = document.createElement('div')
  ele.id = 'ban-menu'
  document.body.appendChild(ele)
  const root = ReactDOM.createRoot(ele)
  root.render(
    <React.StrictMode>
      <Provider>
        <App4 />
      </Provider>
    </React.StrictMode>,
  )
}

export const renderCollection = () => {
  const ele = document.createElement('div')
  ele.id = 'bad-collection-view'
  document.body.appendChild(ele)
  const root2 = ReactDOM.createRoot(ele)
  root2.render(
    <React.StrictMode>
      <Provider>
        <App3 />
      </Provider>
    </React.StrictMode>,
  )
}

addStyles()
renderCollection()
